import React from 'react';
import 'antd/dist/antd.css';
import { PageHeader,Upload,Icon,message,List,Typography} from 'antd';
import '../../static/css/FilePage.css';
import '../../static/css/Layout.css';
import {Result,EachResult} from './Scanner'
import Item from 'antd/lib/list/Item';

function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}
  
function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
}


var Dynamsoft;
let scanner = null;

class FilePage extends React.Component{
    constructor(props){
        super(props);
        this.state={
            showFile:true,
            loading: false,
            resultsInfo:""
        }
    }

    componentDidMount(){
        Dynamsoft = window.Dynamsoft;
        
    }
    

    handleChange = info => {
        // console.log(info.file.originFileObj);
        if (info.file.status === 'uploading') {
            this.setState({ loading: true ,file:info.file.originFileObj});
            return;
        }
        if (info.file.status === 'done') {
        // Get this url from response in real world.
        getBase64(info.file.originFileObj, imageUrl =>
            this.setState({
            imageUrl,
            loading: false,
            }),
        );
        this.decodeFile(info.file.originFileObj);
        }
    };

    decodeFile(file){
        Dynamsoft.BarcodeReader.createInstance().then(r=>{
            r.decode(file).then((results)=>{
                if (results.length > 0) {
                    console.log(results);
                    var txts = [];
                    for (var i = 0; i < results.length; ++i) {
                        txts.push(results[i].BarcodeText);
                    }
                    this.setState({
                        resultsInfo:txts
                    })
                    console.log(txts,typeof txts);
                    //alert(txts.join("\n"));
                    //this.value = '';
                }
                else
				{
                    alert("No barcode found.");
					this.value = '';
				}
            })
        })
    }

    render(){
        const uploadButton = (
            <div className="custom-upload-box">
                <Icon type={this.state.loading ? 'loading' : 'plus'} />
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        const { imageUrl } = this.state;

        const resultList = (
            <div>
                <List
                header={<div>Scanning Result</div>}
                dataSource = {this.state.resultsInfo}
                renderItem={Item=>(
                    <List.Item>
                        <Typography.Text mark>{}</Typography.Text>{Item}
                    </List.Item>
                )}
                >

                </List>
            </div>
        )
        return(
            <>
                
                {/* show the file page */}
                
                <div className="file-container">
                    <PageHeader onBack={this.props.onBackClick} title="Files"  />
                </div>

                <div className="upload-container">
                <div className="upload">
                    <Upload
                        name="avatar"
                        listType="picture-card"
                        showUploadList={false}
                        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                        //beforeUpload={beforeUpload}
                        onChange={this.handleChange}
                        style={{width:180,height:180,display:"table"}}
                    >
                        {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
                    </Upload>    
                </div>
                </div>
                <div>
                {
                    this.state.resultsInfo.length&&
                    // <Result resultsInfo={this.state.resultsInfo}></Result>
                    {resultList}
                }   
                </div>
                
                
                <style jsx>
                    {`
                        .file-container{
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            background-color: white;
                        }
                    `}
                </style>
            </>
            
        )
    }
}
export default FilePage;